.add{
    border: 1px solid;
    width: 300px;
    height: 260px;
    color: #ccc;
    transition: color .25s;
    position: relative;
}


.add::before{
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 80px;
    margin-left: -40px;
    margin-top: -5px;
    border-top: 10px solid;
}

.add::after {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    height: 80px;
    margin-left: -5px;
    margin-top: -40px;
    border-left: 10px solid;
}

.add:hover {
    color: orange;
    cursor: pointer;
}
